export default function picture() {
    //
    var leftones = document.getElementById('detail-main');//leftoffsetTop
    var leftone = document.getElementById('left-image');
    var rightone = document.getElementById('right');
    var box = document.getElementById('box');
    var rimg = rightone.getElementsByTagName("img")[0];
    function getPosition(e) {
        var summ = document.documentElement.scrollTop //
        window.onscroll = function () {
            summ = document.documentElement.scrollTop
        }
        //box
        var top = e.clientY + summ - leftones.offsetTop - box.offsetHeight / 2;
        // console.log(leftone.offsetTop)
        //()
        var left = e.clientX - leftones.offsetLeft - leftone.offsetLeft - box.offsetWidth / 2;
        //
        //
        var maxtop = leftone.offsetHeight - box.offsetHeight;
        //
        var maxleft = leftone.offsetWidth - box.offsetWidth;
        // console.log(maxtop)
        //console.log(maxleft)
        var mintop = 0; //
        var minleft = 0; //
        var mvtop; //
        var mvleft; //
        // 
        if (top < mintop) {
            box.style.top = mintop + "px";
            mvtop = mintop;
        } else if (top > maxtop) {
            box.style.top = maxtop + "px";
            mvtop = maxtop;
        } else {
            box.style.top = top + "px";
            mvtop = top;
        }
        if (left < minleft) {
            box.style.left = minleft + "px";
            mvleft = minleft
        } else if (left > maxleft) {
            box.style.left = maxleft + "px";
            mvleft = maxleft
        } else {
            box.style.left = left + "px";
            mvleft = left;
        }
        rimg.style.top = -mvtop * 2 + "px";
        rimg.style.left = -mvleft * 2 + "px";
    }
    //
    leftone.onmousemove = function (e) {
        box.style.height = leftone.offsetHeight / 2 + "px"
        box.style.display = "block";
        getPosition(e);
        rightone.style.display = "block";
        if (rimg.offsetWidth < 720) {
            box.style.width = "100%"
        }
    }
    //
    leftone.onmouseleave = function () {
        box.style.display = "none";
        rightone.style.display = "none";
    }
}