<!--  -->
<template>
  <div class="detail">
    <Header />
    <div class="detail-main" id="detail-main">
      <div class="altImages">
        <ul>
          <li
            v-for="(item,index) in detailImgList"
            :key="item.id"
            @mousemove="imgmoveFn(index)"
            :class="active==index?'active':''"
          >
            <img :src="item.filePath" alt />
          </li>
        </ul>
        <!-- <div>
          <video src=""></video>
        </div>-->
      </div>
      <div class="image">
        <div class="left-image" id="left-image">
          <img :src="imagePath" alt />
          <div class="slide_box" id="box"></div>
        </div>
        <div class="rightcon" id="right">
          <img :src="imagePath" alt />
        </div>
      </div>
      <div class="detail-text">
        <h2>{{detail.goodsName}}</h2>
        <div class="detail-table">
          <table>
            <tr>
              <td></td>
            </tr>
            <tr>
              <td>1.RGB255*255*255Phome</td>
            </tr>
            <tr>
              <td>2.</td>
            </tr>
            <tr>
              <td>3.85%logologo</td>
            </tr>
            <tr>
              <td>4.</td>
            </tr>
            <tr>
              <td>5.</td>
            </tr>
            <tr>
              <td>6.</td>
            </tr>
            <tr>
              <td></td>
            </tr>
            <tr>
              <td>1.logologo</td>
            </tr>
            <tr>
              <td>2.</td>
            </tr>
            <tr>
              <td>3.</td>
            </tr>
            <tr>
              <td>4.</td>
            </tr>
            <tr>
              <td>5.</td>
            </tr>
          </table>
        </div>
      </div>
    </div>
    <div class="product-image" v-html="detail.goodsDetail"></div>
    <Footer />
  </div>
</template>

<script>
//jsjsjson
import Header from "../components/Header";
import Footer from "../components/footer";
import picture from "../assets/js/image.js";
import Axios from "axios";

export default {
  //import
  components: {
    Header,
    Footer
  },
  data() {
    //
    return {
      active: 0, //
      imagePath: "",
      detail: {},
      detailImgList: []
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    imgmoveFn(index) {
      //
      this.active = index;
      this.imagePath = this.detailImgList[index].filePath;
    },
    getDetail(id) {
      //
      Axios.get(`${this.$store.state.url}sys/goods/details?id=${id}`).then(
        res => {
          //console.log(res.data);
          this.detail = res.data.data.goods; //
          this.detailImgList = res.data.data.img_list; //
          this.imagePath = res.data.data.img_list[0].filePath; //
          picture(); //
        }
      );
    }
  },
  mounted() {
    this.getDetail(this.$route.query.id);
  }
};
</script>
<style lang='less'>
.detail-main {
  width: 1464px;
  margin: 0 auto;
  margin-top: 20px;
  display: flex;
  position: relative;
  .altImages {
    width: 40px;
    ul {
      margin-top: 18px;
      li {
        height: 50px;
        border: 1px solid #adb1b8;
        margin-bottom: 10px;
        cursor: pointer;
        img {
          width: 38px;
          height: 50px;
        }
      }
      li.active {
        border-color: #e77600;
        box-shadow: 0 0 3px 2px rgba(228, 121, 17, 0.5);
      }
    }
  }
  .image {
    width: 685px;
    height: 700px;
    margin-left: 10px;
    text-align: center;
    .left-image {
      display: inline-block;
      position: relative;
      img {
        max-height: 700px;
        max-width: 685px;
        width: auto;
      }
      .slide_box {
        // width: 100%;
        // max-width: 420px;
        width: 360px;
        height: 350px;
        background: rgba(0, 0, 0, 0.3);
        position: absolute;
        top: 0;
        left: 0;
        cursor: pointer;
        display: none;
      }
    }
    .rightcon {
      display: none;
      width: 720px;
      height: 700px;
      background: #fff;
      box-shadow: rgba(0, 0, 0, 0.25) 0px 6px 8px 2px;
      border: 1px solid rgb(148, 148, 148);
      position: absolute;
      top: 0;
      right: 0;
      overflow: hidden;
      text-align: center;
      img {
        display: inline-block;
        position: relative;
        left: 0;
        top: 0;
        width: auto;
        height: 200%;
      }
    }
  }
  .detail-text {
    flex: 1;
    margin-left: 20px;
    .detail-table {
      margin-top: 10px;
      table {
        width: 100%;
        border-collapse: collapse;
        td {
          width: 100%;
          border: 1px solid #ccc;
          padding: 10px 8px;
          font-size: 14px;
          color: #333;
        }
      }
    }
  }
}
.product-image {
  // width: 1464px;
  // margin: 0 auto;
  padding: 0 50px;
  margin-top: 10px;
  border-top: 1px solid #ccc;
  table {
    margin: 0 auto;
  }
  div {
    margin: 0 auto;
  }
  p {
    text-align: center;
    img {
      display: block;
      margin: 0 auto;
    }
  }
}
</style>